import SongPlayer from '@/pages/songPlayer'
export default {

  data() {
    return {
      transitionName: '',
      navbarHeight: 0,
      duration: 400,
      navList: [{
        label: '',
        name: 'singer',
        // link: '/singer'
      }, {
        label: '',
        name: 'songSheet',
        // link: '/songSheet'
      }, {
        label: '',
        name: 'mv',
        link: '/mv'
      }, {
        label: '',
        name: 'search',
        link: '/search'
      }],
    }
  },
  computed: {
    zIndex() {
      return this.$route.name ? {
        zIndex: this.$route.matched[0].meta.index
      } : {}
    },
    pageCls() {
      return this.$route.name ? this.$route.matched[0].name + '-page' : ''
    },
    fullScreenFixed() {
      // console.log(this.$route);
      var matchRoutes = this.$route.matched;
      return matchRoutes[0] && matchRoutes[0].meta.fullScreenFixed
    },
  },
  watch: {
    '$route': function(to, from) {
      if (!to.name || !from.name) {
        return
      }
      this.oldRoute = from
      // console.log('route changed');
      this.setTransitionName(to, from);
    }
  },
  components: { SongPlayer },
  methods: {
    enter(el, done) {
      // console.log('enter', this.$route);
      el.style.zIndex = this.$route.matched[0].meta.index
      setTimeout(done, this.duration)
    },
    afterEnter(el) {
      el.style.removeProperty('z-index')
      // console.log('afterEnter');
    },
    leave(el, done) {
      // console.log('leave', this.$route);
      el.style.zIndex = this.oldRoute.matched[0].meta.index

      setTimeout(done, this.duration)

    },
    afterLeave(el) {
      // console.log('after-leave');
      el.style.removeProperty('z-index')

    },
    setTransitionName(to, from) {
      to = to.matched[0]
      from = from.matched[0]

      const fromIndex = from.meta.index
      const toIndex = to.meta.index
      if (toIndex < fromIndex) {
        this.transitionName = 'prev'
        if (fromIndex >= 4) {

          this.transitionName = 'back'

        }
      } else {
        this.transitionName = 'next'
        if (!to.meta.isHome) {
          this.transitionName = 'forward'

        }

      }

    },
    initPlay() {
      $('audio')[0].play().catch(err => {
        console.error(err);
      })

    }
  }
}




